import React,{useEffect,useState} from "react";
import '../index.css';
import axios from 'axios';
import {Container, Form, FormGroup, Label, Input,Col, Button} from 'reactstrap';
import base_url from "../api/bootapi";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const Purchase =() => {
  useEffect(()=>{
    document.title="AddProducts";
  },[]);
  const [pproduct,setpproducts]=useState([]);
  const handleForm =(e) =>{
    console.log(pproduct);
    postDatatoServer(pproduct);
    e.preventDefault();
  };
  const postDatatoServer =(data)=>{
    axios.post(`${base_url}/pproducts`,data).then(
      (response) => {
console.log(response);
console.log("success");
toast.success("Data Saved")
},
(error) =>{
  console.log(error);
  console.log("error");
  toast.error("Data is not saved")
}
    );
     Location.reload();
  };
  // const doAction= ()=> {
  //   // eslint-disable-next-line
  //   this.state.total=parseInt(this.state.pprice) * parseInt(this.state.pquantity);
  //   debugger;this.setState({tprice: this.state.total })
  //   debugger;
  // }
  const dotprice= (e) =>{
   let tp=pproduct.pquantity*pproduct.pprice;
setpproducts({
  ...pproduct,
  ptotalprice: tp
});
} // {setpproducts({...pproduct,pcategory :e.target.value})}}
debugger;const pcategoryCheck= (e) =>{
 debugger; 
    
    const { target: { value } } = e;
    debugger;if(value==="Dairy"){
    
      var a4=document.getElementById("fruitslabel");
     var b4=document.getElementById("pfruits");
     a4.style.display = "none";
     b4.style.display = "none";
  
      var a=document.getElementById("dairylabel");
     var b=document.getElementById("pdairy");
     a.style.display = "block";
     b.style.display = "block";
  }
  if(value==="Fruits"){
    var a2=document.getElementById("dairylabel");
     var b2=document.getElementById("pdairy");
     a2.style.display = "none";
     b2.style.display = "none";
    var a1=document.getElementById("fruitslabel");
     var b1=document.getElementById("pfruits");
     a1.style.display = "block";
     b1.style.display = "block";
  
 }
 setpproducts({...pproduct,pcategory :e.target.value})
}
debugger;const pnameCheck= (e) =>{
  debugger; 
     
     //const { target: { value } } = e;
     setpproducts({...pproduct,pname:e.target.value})
    }
  return (
    <Container>
    <Form onSubmit = {handleForm}>
    <FormGroup row>
      <Col><h1 style={{color:'#00BFFF', paddingTop:'5%'}}>Product Purchase</h1></Col>
    </FormGroup>
      <FormGroup row>
        <Label for="pid" sm={2} style={{color:' #3a34eb'}}>Purchase ID</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="pid"
          id="pid"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setpproducts({...pproduct,pid :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row >
        <Label for="pcategory" sm={2} style={{color:' #3a34eb'}}>Product Category</Label>
        <Col sm={4}>
        <Input type="select" name="pcagtegory" id="pcategory"  onChange={(e)=>pcategoryCheck(e)} value={pproduct.pcategory}style={{color:' #3a34eb'}}>
        <option value="selected" selected="selected">Select a Product Category</option>
          <option value="Dairy">Dairy</option>
          <option value="Fruits">Fruits</option>
          onChange={(e)=>{setpproducts({...pproduct,pcategory :e.target.value})}}
          {/* onSelect={(e)=>dotprice(e)}
          value={pproduct.tprice} */}
        </Input>
        </Col>
      </FormGroup>
      
      <FormGroup row>
        <Label for="pname" sm={2} style={{color:' #3a34eb',display: 'none'}} id="dairylabel" >Dairy</Label>
        <Col sm={4}>
        <Input type="select" name="pname" id="pdairy" style={{color:' #3a34eb',display: 'none'}} onChange={(e)=>pnameCheck(e)} value={pproduct.pname}>
        <option value="selected" selected="selected">Select a Product</option>
          <option>Milk</option>
          <option>Cream</option>
          <option>Cheese</option>
          onChange={(e)=>{setpproducts({...pproduct,pname :e.target.value})}}
          
        </Input>
       </Col>
      </FormGroup>
      
      <FormGroup row>
        <Label for="pname" sm={2} id="fruitslabel" style={{color:' #3a34eb',display: 'none' }}>Fruits</Label>
        <Col sm={4}>
        <Input type="select" name="pname" id="pfruits" style={{color:' #3a34eb',display: 'none'}} onChange={(e)=>pnameCheck(e)} value={pproduct.pname} >
        <option value="selected" selected="selected">Select a Product</option>
          <option>Bananas</option>
          <option>Apples</option>
          <option>Oranges</option>
          onChange={(e)=>{setpproducts({...pproduct,pname :e.target.value})}}
        </Input>
        </Col>
      </FormGroup>
      {/* <FormGroup row>
        <Label for="pname" sm={2} style={{color:' #3a34eb'}}>Product Name</Label>
        <Col sm={4}>
        <Input
          type="text"
          name="pname"
          id="pname"
          placeholder=" "
          style={{color:' #3a34eb'}}
        /></Col>
      </FormGroup> */}
      <FormGroup row>
        <Label for="pcode" sm={2} style={{color:' #3a34eb'}}>Product Code</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="pcode"
          id="pcode"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setpproducts({...pproduct,pcode :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="pdate" sm={2} style={{color:' #3a34eb'}}>Purchase Date</Label>
        <Col sm={4}>
        <Input
          type="date"
          name="pdate"
          id="pdate"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setpproducts({...pproduct,pdate :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="pquantity" sm={2} style={{color:' #3a34eb'}} >Purchase Quantity</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="pquantity"
          id="pquantity"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setpproducts({...pproduct,pquantity :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="pprice" sm={2} style={{color:' #3a34eb'}}>Purchase Price</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="pprice"
          id="pprice"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setpproducts({...pproduct,pprice :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="ptotalprice" sm={2} style={{color:' #3a34eb'}}>Total Price</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="ptotalprice"
          id="ptotalprice"
          placeholder=" "
          style={{color:' #3a34eb'}}
          onSelect={(e)=>dotprice(e)}
          value={pproduct.ptotalprice}
        /></Col>
      </FormGroup>
      <FormGroup row>
      <Col sm={4} style={{color:'#00BFFF', paddingTop:'1%',paddingLeft:'30%'}}><div><Button type="submit">SUBMIT</Button><ToastContainer position="bottom-right" autoClose="12000" closeOnClick /></div></Col>
      
      
    </FormGroup>
    </Form>
    </Container>
  );
}


export default Purchase;