import React,{useEffect,useState} from "react";
import '../index.css';
import {Container,Form, Row, Col,FormGroup, Label, Input, Button } from 'reactstrap';
import { ToastContainer,toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import base_url from "./../api/bootapi";

const BorderPage =() => {
  
  useEffect(()=>{
    document.title="Profit Calculator";
  },[]);
  const [pproduct,setpproducts]=useState([]);
  /////////////////Handle form one//////////////////////// 
  const handleFormOne =(e) =>{
    console.log(pproduct);
    getDataFromServer1(pproduct);
    toast.info(pproduct);
    e.preventDefault();
  };
  const getDataFromServer1 = () => {
    debugger;axios.get(`${base_url}/profitdate/${pproduct.pdate}`).then((response) => {
     
     
      debugger;console.log(response.data);
      setpproducts(response.data);
      toast.info(response.data);
    },
    (error) => {
      debugger;console.log(error);
    }
    );
    
   };
  ////////////handle form two//////////////////////////////////////////
  const handleFormTwo =(e) =>{
    console.log(pproduct);
    getDataFromServer(pproduct);
    toast.info(pproduct);
    e.preventDefault();
  };
  const getDataFromServer = () => {
    debugger;axios.get(`${base_url}/profityear/${pproduct.pyear}/${pproduct.pcategory}`).then((response) => {
     
     
      debugger;console.log(response.data);
      setpproducts(response.data);
    },
    (error) => {
      debugger;console.log(error);
    }
    );
    
   };
   //////////////////////////Handle form three/////////////////////////////////
   const handleFormThree =(e) =>{
    console.log(pproduct);
    getDataFromServer3(pproduct);
    toast.info(pproduct);
    e.preventDefault();
  };
  const getDataFromServer3 = () => {
    debugger;axios.get(`${base_url}/profitbyname/${pproduct.pyear}/${pproduct.pname}`).then((response) => {
     
     
      debugger;console.log(response.data);
      setpproducts(response.data);
      if(response.data){
      toast.info(response.data);
    }
    else{
      toast.info("Record Not Found");
    }
    },
    (error) => {
      debugger;console.log(error);
    }
    );
    
   };
   /////////////////////////Handle form four//////////////////////////////////////
   const handleFormFour =(e) =>{
    console.log(pproduct);
    getDataFromServer4(pproduct);
    toast.info(pproduct);
    e.preventDefault();
  };
  const getDataFromServer4 = () => {
    debugger;axios.get(`${base_url}/profitbyprice/${pproduct.pyear}/${pproduct.pprice}`).then((response) => {
     
     
      debugger;console.log(response.data);
      setpproducts(response.data);
      toast.info(response.data);
    },
    (error) => {
      debugger;console.log(error);
    }
    );
    
   };
return (
<>
   
    <Container>
        <Form name="one">
          <table>
            <tr>
              <td>
        <span className="block-example border border-primary">
    <Row>
      <Col><h6 style={{color:'#00BFFF', paddingTop:'5%', paddingLeft:'5%'}}>Profit According To Date</h6></Col>
    </Row>
    <br></br>
    <FormGroup row>
            <Label for="pdate" sm={2} style={{color:' #3a34eb', paddingLeft:'10%'}}>Date</Label>
            <Col sm={9}>
            <Input
              type="date"
              name="pdate"
              id="pdate"
              placeholder="yyyy-MM-dd "
              style={{color:' #3a34eb'}}
              pattern="yyyy-MM-dd" 
              required
              onChange={(e)=>{setpproducts({...pproduct,pdate :e.target.value})}}
            /></Col>
          </FormGroup>
          <br></br>
          <br></br>
          <FormGroup row>
          <Col sm={4} style={{color:'#00BFFF', paddingTop:'1%',paddingLeft:'30%'}}><div><Button onClick = {handleFormOne}>SUBMIT</Button>
          <ToastContainer position="top-center" autoClose={15000} closeOnClick rtl={false} hideProgressBar  /></div></Col>
        
        </FormGroup>
        </span>
</td>
</tr>
</table>
</Form>
<Form name="two" >
  <table>
    <tr>
<td>
          <span className="block-example border border-primary">
    
        
    <Row>
      <Col><h6 style={{color:'#00BFFF', paddingTop:'5%',paddingLeft:'5%'}}>Profit According To Year And Category</h6></Col>
    </Row>
    <br></br>
   
    <FormGroup row>
            <Label for="pyear" sm={6} style={{color:' #3a34eb',paddingLeft:'10%'}}>Year</Label>
            <Col sm={5}>
            <Input
              type="text"
              name="pyear"
              id="pyear"
              placeholder=" "
              style={{color:' #3a34eb'}}
              required
              onChange={(e)=>{setpproducts({...pproduct,pyear :e.target.value})}}
            /></Col>
          </FormGroup>
          <FormGroup row>
            <Label for="pcategory" sm={6} style={{color:' #3a34eb',paddingLeft:'10%'}}>Product Category</Label>
            <Col sm={5}>
            <Input type="select" name="select" id="pcategory"  onChange={(e)=>{setpproducts({...pproduct,pcategory :e.target.value})}} value={pproduct.pcategory} style={{color:' #3a34eb'} } required>
            <option value="selected" selected="selected">Select Category</option>
              <option>Dairy</option>
              <option>Fruits</option>
            </Input>
           
            </Col>
          </FormGroup>
          <FormGroup row>
          <Col sm={4} style={{color:'#00BFFF', paddingTop:'1%',paddingLeft:'30%'}}><div><Button onClick = {handleFormTwo}>SUBMIT</Button>
          <ToastContainer position="top-center" autoClose={15000} closeOnClick rtl={false} hideProgressBar  /></div></Col>
        </FormGroup>
        </span>
        </td>
        </tr>
</table>
</Form>
<Form name="three">
  <table>

        <tr>

              <td>
        <span className="block-example border border-primary">
    <Row>
      <Col><h6 style={{color:'#00BFFF', paddingTop:'5%', paddingLeft:'5%'}}>Profit According To Year And Name</h6></Col>
    </Row>
    <br></br>
    <FormGroup row>
            <Label for="pdate" sm={6} style={{color:' #3a34eb',paddingLeft:'10%'}}>Year</Label>
            <Col sm={5}>
            <Input
              type="number"
              name="pyear"
              id="pyear"
              placeholder=" "
              style={{color:' #3a34eb'}}
              required
              onChange={(e)=>{setpproducts({...pproduct,pyear :e.target.value})}}
            /></Col>
          </FormGroup>
          <FormGroup row>
            <Label for="pname" sm={6} style={{color:' #3a34eb',paddingLeft:'10%'}}>Product Name</Label>
            <Col sm={5}>
            <Input type="select" name="select" id="pname" onChange={(e)=>{setpproducts({...pproduct,pname :e.target.value})}} value={pproduct.pname} style={{color:' #3a34eb'}} required>
            <option value="selected" selected="selected">Select Product Name</option>
              <option>Milk</option>
              <option>Cream</option>
              <option>Cheese</option>
              <option>Bananas</option>
              <option>Apples</option>
              <option>Oranges</option>
            </Input>
            </Col>
          </FormGroup>
          
          <FormGroup row>
          <Col sm={4} style={{color:'#00BFFF', paddingTop:'1%',paddingLeft:'30%'}}><div><Button onClick = {handleFormThree}>SUBMIT</Button>
          <ToastContainer position="top-center" autoClose={15000} closeOnClick rtl={false} hideProgressBar  /></div></Col>
        
        </FormGroup>
        </span>
</td>
</tr>
</table>
</Form>
<Form name="four">
  <table>
    <tr>
<td>
          <span className="block-example border border-primary">
    
        
    <Row>
      <Col><h6 style={{color:'#00BFFF', paddingTop:'5%',paddingLeft:'5%'}}>Profit According To Year And Price</h6></Col>
    </Row>
    <br></br>
   
    <FormGroup row>
            <Label for="pdate" sm={6} style={{color:' #3a34eb',paddingLeft:'10%'}}>Year</Label>
            <Col sm={5}>
            <Input
              type="number"
              name="pyear"
              id="pyear"
              placeholder=" "
              style={{color:' #3a34eb'}}
              required
              onChange={(e)=>{setpproducts({...pproduct,pyear :e.target.value})}}
            /></Col>
          </FormGroup>
          <FormGroup row>
            <Label for="pprice" sm={6} style={{color:' #3a34eb',paddingLeft:'10%'}}>Price</Label>
            <Col sm={5}>
            <Input
              type="number"
              name="pprice"
              id="pprice"
              placeholder=" "
              style={{color:' #3a34eb'}}
              required
              onChange={(e)=>{setpproducts({...pproduct,pprice :e.target.value})}}
            /></Col>
          </FormGroup>
          <FormGroup row>
          <Col sm={4} style={{color:'#00BFFF', paddingTop:'1%',paddingLeft:'30%'}}><div><Button onClick = {handleFormFour}>SUBMIT</Button>
          <ToastContainer position="top-center" autoClose={15000} closeOnClick rtl={false} hideProgressBar  /></div></Col>
        
        </FormGroup>
        </span>
        </td>
        </tr>
        </table>
        </Form>

          </Container>
         
        
          
    
</>
);
}
export default BorderPage;