import React,{useEffect,useState} from "react";
import '../index.css';
import axios from 'axios';
import base_url from "../api/bootapi";

const PurchasePage = () => {

     const getDataFromServer = () => {
  debugger;axios.get(`${base_url}/pproducts`).then((response) => {
   
   
    debugger;console.log(response.data);
    setPproducts(response.data);
  },
  (error) => {
    debugger;console.log(error);
  }
  );
  
 };
    
  useEffect(() => {
   debugger; getDataFromServer();
  },[]);
  const[pproducts,setPproducts] = useState([]);
return (
   

    <div>
        <h1 style={{color:'#00BFFF'}}>List Of Purchased Products</h1>
        <table border="2" bordercolor="#00BFFF">
        <tr>
            <th>Product ID</th>
            <th>Product Category</th>
            <th> Product Name</th>
            <th>Product Code</th>
            <th>Purchase Date</th>
            <th>Product Quantity</th>
            <th>Purchase Price</th>
            <th>Total Price</th>
            </tr>
        </table>
      {pproducts.length>0
        ? pproducts.map(({pid,pcategory,pname,pcode,pdate,pquantity,pprice,ptotalprice}) => (<table border="2" bordercolor="#00BFFF">

          <tr>
            <td>{pid}</td>
            <td> {pcategory}</td>
            <td>{pname}</td>
            <td>{pcode}</td>
            <td>{pdate}</td>
            <td>{pquantity}</td>
            <td>{pprice}</td>
            <td>{ptotalprice}</td>
            </tr></table>))
        :"No Products"}

    </div>
        
);
}
export default PurchasePage;