import React, { Component } from "react";
import {Container, Row, Col,FormGroup, Label, Input } from 'reactstrap';
import Purchase from '../components/Purchase';
import Sale from '../components/Sale';
import Profitcalculator from '../components/Profitcalculator';
import PurchasePage from '../components/PurchasePage';
import SalePage from '../components/SalePage';
class Mainradio extends Component {
    constructor(props) {
      super(props);
      this.state = { status: 0 }; // 0: no show, 1: show yes, 2: show no.
    }

    radioHandler = (status) => {
      this.setState({status});
    }

render() {
    const { status } = this.state;
  return (
    <Container>
    <Row>
      <Col><h1 style={{color:'#00BFFF', paddingTop:'10%'}}>Product Management System</h1></Col>
    </Row>
    <FormGroup check>
        <Label check>
          <Input type="radio"  name="radio1" id="purchase" checked={status === 1}
            onClick={(e) => this.radioHandler(1)} />{' '}
         <p class="text-left"  style={{color:'#00BFFF'}}><h6>Purchase</h6> </p>
        </Label>
      </FormGroup>
      <FormGroup check>
        <Label check>
          <Input type="radio" name="radio1" id="sale"  checked={status === 2}
            onClick={(e) => this.radioHandler(2)}/>{' '}
          <p class="text-left" style={{color:'#00BFFF'}}> <h6>Sale</h6></p>
        </Label>
        
      </FormGroup>
      <FormGroup check>
        <Label check>
          <Input type="radio"  name="radio1" id="profitcalculator" checked={status === 3}
            onClick={(e) => this.radioHandler(3)} />{' '}
         <p class="text-left"  style={{color:'#00BFFF'}}><h6>Profit Calculator</h6> </p>
        </Label>
      </FormGroup>
      <FormGroup check>
        <Label check>
          <Input type="radio" name="radio1" id="purchaselist"  checked={status === 4}
            onClick={(e) => this.radioHandler(4)}/>{' '}
          <p class="text-left" style={{color:'#00BFFF'}}> <h6> Purchase List</h6></p>
        </Label>
        </FormGroup>
      <FormGroup check>
        <Label check>
          <Input type="radio"  name="radio1" id="profitcalculator" checked={status === 5}
            onClick={(e) => this.radioHandler(5)} />{' '}
         <p class="text-left"  style={{color:'#00BFFF'}}><h6>Sale List</h6> </p>
        </Label>
        {status === 1 && (<div><Purchase /></div>)}
        {status === 2 && (<div><Sale /></div>)}
        {status === 3 && (<div><Profitcalculator /></div>)}
        {status === 4 && (<div><PurchasePage /></div>)}
        {status === 5 && (<div><SalePage /></div>)}
      
      </FormGroup>
      </Container>
   
  );
}
}

export default Mainradio;
