import React,{useEffect,useState} from "react";
import '../index.css';
import axios from 'axios';
import {Container, Form, FormGroup, Label, Input,Col, Button} from 'reactstrap';
import base_url from "../api/bootapi";
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
//import $ from 'jquery';


const Sale =() => {
  useEffect(()=>{
    document.title="Sale Products";
  },[]);
  const [sproduct,setsproducts]=useState([]);
  const handleForm =(e) =>{
    console.log(sproduct);
    postDatatoServer(sproduct);
    e.preventDefault();
  };
  const postDatatoServer =(data)=>{
    axios.post(`${base_url}/sproducts`,data).then(
      (response) => {
console.log(response);
console.log("success");
toast.success("Data Saved")
},
(error) =>{
  console.log(error);
  console.log("error");
  toast.error("Data is not saved")
}
    );
    Location.reload();
  };
  // const doAction= ()=> {
  //   // eslint-disable-next-line
  //   this.state.total=parseInt(this.state.sprice) * parseInt(this.state.squantity);
  //   debugger;this.setState({stprice: this.state.total })
  //   debugger;
  // }
  const dostprice= (e) =>{
   let tp=sproduct.squantity*sproduct.sprice;
setsproducts({
  ...sproduct,
  stotalprice: tp
});
} // {setsproducts({...sproduct,scategory :e.target.value})}}
debugger;const scategoryCheck= (e) =>{
 debugger; 
    
    const { target: { value } } = e;
    debugger;if(value==="Dairy"){
    
      var a4=document.getElementById("fruitslabel");
     var b4=document.getElementById("pfruits");
     a4.style.display = "none";
     b4.style.display = "none";
  
      var a=document.getElementById("dairylabel");
     var b=document.getElementById("pdairy");
     a.style.display = "block";
     b.style.display = "block";
  }
  if(value==="Fruits"){
    var a2=document.getElementById("dairylabel");
     var b2=document.getElementById("pdairy");
     a2.style.display = "none";
     b2.style.display = "none";
    var a1=document.getElementById("fruitslabel");
     var b1=document.getElementById("pfruits");
     a1.style.display = "block";
     b1.style.display = "block";
  
 }
 setsproducts({...sproduct,scategory :e.target.value})
}
debugger;const snameCheck= (e) =>{
  debugger; 
     
     //const { target: { value } } = e;
     setsproducts({...sproduct,sname:e.target.value})
    }
  return (
    <Container>
    <Form onSubmit = {handleForm}>
    <FormGroup row>
      <Col><h1 style={{color:'#00BFFF', paddingTop:'5%'}}>Product Sale</h1></Col>
    </FormGroup>
      <FormGroup row>
        <Label for="sid" sm={2} style={{color:' #3a34eb'}}>Sale ID</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="sid"
          id="sid"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setsproducts({...sproduct,sid :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row >
        <Label for="scategory" sm={2} style={{color:' #3a34eb'}}>Product Category</Label>
        <Col sm={4}>
        <Input type="select" name="pcagtegory" id="scategory"  onChange={(e)=>scategoryCheck(e)} value={sproduct.scategory}style={{color:' #3a34eb'}}>
        <option value="selected" selected="selected">Select a Product Category</option>
          <option value="Dairy">Dairy</option>
          <option value="Fruits">Fruits</option>
          onChange={(e)=>{setsproducts({...sproduct,scategory :e.target.value})}}
          {/* onSelect={(e)=>dostprice(e)}
          value={sproduct.stprice} */}
        </Input>
        </Col>
      </FormGroup>
      
      <FormGroup row>
        <Label for="sname" sm={2} style={{color:' #3a34eb',display: 'none'}} id="dairylabel" >Dairy</Label>
        <Col sm={4}>
        <Input type="select" name="sname" id="pdairy" style={{color:' #3a34eb',display: 'none'}} onChange={(e)=>snameCheck(e)} value={sproduct.sname}>
        <option value="selected" selected="selected">Select a Product</option>
          <option>Milk</option>
          <option>Cream</option>
          <option>Cheese</option>
          onChange={(e)=>{setsproducts({...sproduct,sname :e.target.value})}}
          
        </Input>
       </Col>
      </FormGroup>
      
      <FormGroup row>
        <Label for="sname" sm={2} id="fruitslabel" style={{color:' #3a34eb',display: 'none' }}>Fruits</Label>
        <Col sm={4}>
        <Input type="select" name="sname" id="pfruits" style={{color:' #3a34eb',display: 'none'}} onChange={(e)=>snameCheck(e)} value={sproduct.sname} >
        <option value="selected" selected="selected">Select a Product</option>
          <option>Bananas</option>
          <option>Apples</option>
          <option>Oranges</option>
          onChange={(e)=>{setsproducts({...sproduct,sname :e.target.value})}}
        </Input>
        </Col>
      </FormGroup>
      {/* <FormGroup row>
        <Label for="sname" sm={2} style={{color:' #3a34eb'}}>Product Name</Label>
        <Col sm={4}>
        <Input
          type="text"
          name="sname"
          id="sname"
          placeholder=" "
          style={{color:' #3a34eb'}}
        /></Col>
      </FormGroup> */}
      <FormGroup row>
        <Label for="scode" sm={2} style={{color:' #3a34eb'}}>Product Code</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="scode"
          id="scode"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setsproducts({...sproduct,scode :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="sdate" sm={2} style={{color:' #3a34eb'}}>Sale Date</Label>
        <Col sm={4}>
        <Input
          type="date"
          name="sdate"
          id="sdate"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setsproducts({...sproduct,sdate :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="squantity" sm={2} style={{color:' #3a34eb'}} >Sale Quantity</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="squantity"
          id="squantity"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setsproducts({...sproduct,squantity :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="sprice" sm={2} style={{color:' #3a34eb'}}>Sale Price</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="sprice"
          id="sprice"
          placeholder=" "
          style={{color:' #3a34eb'}}
          required
          onChange={(e)=>{setsproducts({...sproduct,sprice :e.target.value})}}
        /></Col>
      </FormGroup>
      <FormGroup row>
        <Label for="stotalprice" sm={2} style={{color:' #3a34eb'}}>Total Price</Label>
        <Col sm={4}>
        <Input
          type="number"
          name="stotalprice"
          id="stotalprice"
          placeholder=" "
          style={{color:' #3a34eb'}}
          onSelect={(e)=>dostprice(e)}
          value={sproduct.stotalprice}
        /></Col>
      </FormGroup>
      <FormGroup row>
      <Col sm={4} style={{color:'#00BFFF', paddingTop:'1%',paddingLeft:'30%'}}><div><Button type="submit">SUBMIT</Button><ToastContainer position="bottom-right" autoClose="2000" /></div></Col>
      
      
    </FormGroup>
    </Form>
    </Container>
  );
}


export default Sale;