import React,{useEffect,useState} from "react";
import '../index.css';
import axios from 'axios';
import base_url from "../api/bootapi";

const SalePage = () => {

     const getDataFromServer = () => {
  debugger;axios.get(`${base_url}/sproducts`).then((response) => {
   
   
    debugger;console.log(response.data);
    setSproducts(response.data);
  },
  (error) => {
    debugger;console.log(error);
  }
  );
  
 };
    
  useEffect(() => {
   debugger; getDataFromServer();
  },[]);
  const[sproducts,setSproducts] = useState([]);
return (
   

    <div>
        <h1 style={{color:'#00BFFF'}}>List Of Sold Products</h1>
        <table border="2" bordercolor="#00BFFF">
        <tr>
            <th>Product ID</th>
            <th>Product Category</th>
            <th> Product Name</th>
            <th>Product Code</th>
            <th>Sale Date</th>
            <th>Product Quantity</th>
            <th>Sale Price</th>
            <th>Total Price</th>
            </tr>
        </table>
      {sproducts.length>0
        ? sproducts.map(({sid,scategory,sname,scode,sdate,squantity,sprice,stotalprice}) => (<table border="2" bordercolor="#00BFFF">

          <tr>
            <td>{sid}</td>
            <td> {scategory}</td>
            <td>{sname}</td>
            <td>{scode}</td>
            <td>{sdate}</td>
            <td>{squantity}</td>
            <td>{sprice}</td>
            <td>{stotalprice}</td>
            </tr></table>))
        :"No Products"}

    </div>
        
);
}
export default SalePage;